import { defaultLogger } from '../logger.js';
export const logger = defaultLogger.child({
    name: 'rpc'
});
export class ResponseError extends Error {
    code;
    constructor(code, message){
        super(message);
        this.name = 'ResponseError';
        this.code = code;
    }
    toJSON() {
        return {
            code: this.code,
            message: this.message
        };
    }
}
